<template>
	<div class="container">
		<div class="content">
			<div class="top">
				<div class="left box box1">
					<div class="top-list-box">
                        <div class="item">
                            <div class=" center-center value">{{(overview.EquipmentCount==null)?"Loading":numberComma(overview.EquipmentCount)}}</div>
                            <div class=" center-center text">设备数量（台）</div>
                        </div>
                        <div class="item">
                            <div class=" center-center value">{{(overview.EquipmentAmount==null)?"Loading":numberComma(Math.floor(overview.EquipmentAmount/10000))}}</div>
                            <div class=" center-center text">设备金额（万元）</div>
                        </div>
                        <div class="item">
                            <div class=" center-center value">{{(overview.EquipmentAmount==null)?"Loading":((overview.DepreciationRate*100).toFixed(2)+"%")}}</div>
                            <div class=" center-center text">折旧率</div>
                        </div>
                        <div class="item">
                            <div class=" center-center value">{{(overview.ServiceCount==null)?"Loading":numberComma(overview.ServiceCount)}}</div>
                            <div class=" center-center text">当年服务人次(人)</div>
                        </div>
					</div>
					<div class="chart1-box">
						<div style="position: absolute;	"><span style="border-left: 5px solid #86C7E6;
						border-right: 5px solid #86C7E6;
						border-bottom: 5px solid #86C7E6;
						border-top: 5px solid #86C7E6;display: inline-block;
						"></span>&nbsp;收入</div>
						<div style="
						position: absolute;
						top: 1rem;
					"><span style="border-left: 5px solid #1B85E7;
						border-right: 5px solid #1B85E7;
						border-bottom: 5px solid #1B85E7;
						border-top: 5px solid #1B85E7;display: inline-block;
						"></span>&nbsp;支出</div>
						<div style="
						position: absolute;
						top: 1.5rem;
					"><span style="border-left: 5px solid red;
						border-right: 5px solid red;
						border-bottom: 5px solid red;
						border-top: 5px solid red;display: inline-block;
						"></span>&nbsp;盈亏</div>
					<!-- <div style="position: absolute;top:3rem;"><span>12312</span></div> -->
						<div class="list-box1">
							<div class="list">
								<div class="total">
									<div class=" center-center value ">{{(totalIncome==null)?"Loading":numberComma(Math.floor(Number(totalIncome/10000)))}}</div>
                                    <div class=" center-center text">总收入（万元）</div>
                                </div>
                                <div :class="(totalIncome-totalIncomeLast)>0?'status up green':'status down red'">
                                    <div class=" center-center value">
                                        <span v-if="totalIncomeLast == 0">0.00%</span>
                                        <span v-else>{{(totalIncome==null)?"Loading":Math.abs(Number((totalIncome-totalIncomeLast)*1.0/totalIncomeLast*100).toFixed(2))}}%</span>
                                        <span class="iconfont icon-shang"></span>
                                    </div>
                                    <div class=" center-center text">
                                        同比去年
                                    </div>
                                </div>
                            </div>
                            <div class="list">
                                <div class="total">
                                    <div class=" center-center value">{{(totalExpense==null)?"Loading":numberComma(Math.floor(Number(totalExpense/10000)))}}</div>
                                    <div class=" center-center text">总支出（万元）</div>
                                </div>
                                <div :class="(totalExpense-totalExpenseLast)>0?'status up green':'status down red'">
                                    <div class=" center-center value">
                                        <span v-if="totalExpenseLast == 0">0.00%</span>
                                        <span v-else>{{(totalExpense==null)?"Loading":Math.abs(Number((totalExpense-totalExpenseLast)*1.0/totalExpenseLast*100).toFixed(2))}}%</span>
                                        <span class="iconfont icon-shang"></span>
                                    </div>
                                    <div class=" center-center text">
                                        同比去年
									</div>
								</div>
							</div>
						</div>
						<div id="chart1"></div>
					</div>
				</div>
				<div class="right box box2">
					<EventList></EventList>
				</div>
			</div>
			<div class="bottom">
				<div class="left box box3">
                    <div id="chart3"></div>
                    <div class="list-box-container">
                        <div class="list-box3" :style="chart3.data.length>0 && chart3.data[0].name=='暂无报修'?'display: flex;align-items: center;justify-content: center;':''">
                            <div class="list" v-for="(item,key) of list3" :key="key" v-show="chart3.data.length>0 && chart3.data[0].name!='暂无报修'">
                                <span class="index">{{key+1}}</span>
                                <span class="name elip" :title="item.name">{{item.name}}</span>
                                <span class="status" :class="{'err':item.status}">{{item.statusDesc}}</span>
                            </div>
							<div v-show="chart3.data.length>0 && chart3.data[0].name=='暂无报修'" class="repairNull">
								<div class="repairText">暂无报修</div>
							</div>
                        </div>
                    </div>
                </div>
                <div class="right box box4">
					<KpiList></KpiList>
				</div>
			</div>
		</div>

	</div>
</template>
<script>
    import {} from '@/api/api'
    import G2 from '@antv/g2';
    import {DataSet} from '@antv/data-set'
    import {queryOverview, departmentIncome, requestToday} from '@/api/api'
    import {getRem} from '@/common/common'
    import EventList from '../../components/componentEventList'
    import KpiList from '../../components/componentKpiGauge'

	export default {
		components: {
			EventList,
			KpiList,
		},

		data() {
			return {
				overview: '',
				chart1: {
					data: [],
					startIndex:1
				},
				totalIncome: 0.00,
				totalExpense: 0.00,
				totalIncomeLast: 0.00,
				totalExpenseLast: 0.00,
				chart3: {
					data: [],
					interval: null,
					activeIndex: 0,
					timing: null,
				},
				list3: [],

			}
		},
		computed: {},
        created() {
			this.getCurrUserVue()
			this.getqueryOverview()
			this.getRequestToday()
			this.getIncome()
			//this.autoSelectchart3()
            this.$nextTick(() => {
            })
			this.autoSelectchart3()
		},
		methods: {
			getCurrUserVue() {
				if (roleID < 0)
					window.location.href = BASE_URL + "/Home/Login";
				else if (roleID == 1)
					this.$router.push("indexSA");
				else if (roleID == 2)
					this.$router.push("indexA");
			},
			getqueryOverview() {
				queryOverview().then(res => {
					this.overview = res.Data;
				})
			},
			getRequestToday() {
				requestToday().then(res => {
					var data = res.Data;
					var _list = [];
					for (var i = 0; i < data.length; i++) {
						var ret = _list.findIndex((val) => {
							return val.name == data[i].DepartmentName;
						})
						if (ret == -1) {
                            _list.push({
                                name: data[i].DepartmentName,
                                count: 1,
                                list: [
                                    {
                                        name: ' 主题：'+data[i].SubjectName + ' 设备编号：' + data[i].EquipmentOID + ' 科室：' + data[i].DepartmentName,
                                        status: data[i].Status.ID != 99 ? true : false,
                                        statusDesc: data[i].Status.Name
                                    }
                                ]
                            })
                        } else {
                            _list[ret].count ++;
                            _list[ret].list.push({
                                name: ' 主题：'+data[i].SubjectName + ' 设备编号：' + data[i].EquipmentOID + ' 科室：' + data[i].DepartmentName,
                                status: data[i].Status.ID != 99 ? true : false,
                                statusDesc: data[i].Status.Name
                            })
                        }
                    }
                    this.chart3.data = _list;
					if(!data.length){
						var nullList=[]
						nullList.push({
                                name: "暂无报修",
                                count: 1,
                                list: [
                                    {
                                        name: '暂无报修',
                                        status: true,
                                        statusDesc: '正常'
                                    }
                                ]
                            })
						this.chart3.data =nullList
					}
					this.renderChart3()
				})
			},
			getIncome() {
				departmentIncome().then(res => {
					let t_income = 0.00,
						t_expense = 0.00,
						t_income_last = 0,
						t_expense_last = 0
					let data = res.Data;
					let d_list = [];
					for (let i = 0; i < data.length; i++) {
						t_income += Number(data[i].Incomes);
						t_income_last += Number(data[i].LastIncomes);
						t_expense += Number(data[i].Expenses);
						t_expense_last += Number(data[i].LastExpenses);
						let json = {
							id: data[i].Department.ID,
							name: data[i].Department.Name,
							'收入': Number(data[i].Incomes/10000),
							'支出': Number(data[i].Expenses/10000),
							'设备数量': data[i].EquipmentCount,
                            '设备价值': Number(data[i].EquipmentAmount / 10000),
							'服务人次': data[i].ServiceCount,
						}
						d_list.push(json)
					}
					this.chart1.data = d_list;
					this.totalIncome = t_income;
					this.totalExpense = t_expense;
					this.totalIncomeLast = t_income_last;
					this.totalExpenseLast = t_expense_last;
					this.renderChart1()
				})
			},
			
			// setChartData(data)
			// {
			// 	var dv = new DataSet.DataView();
			// 	dv.source(data).transform({
			// 			type: 'map',
			// 			callback: function callback(row) {
			// 				row['支出'] *= -1;
			// 				return row;
			// 			}
			// 		})
			// 		.transform({
			// 			type: 'fold',
			// 			fields: ['收入', '支出'],
			// 			key: 'opinion',
			// 			value: 'value',
			// 			retains: ['name']
			// 		});
			// 		
			// 		return dv;
			// },
			renderChart1() {
				// let rem =
				let _this = this
				
				// var newData = new Array();
				// var index = 0;
				// this.chart1.data.forEach(n => {
				// 	index++;
				// 	if (index >= this.chart1.startIndex && newData.length < 10)
				// 		newData.push(n);
				// })
				
				//var dv = this.setChartData(newData);
				var dv = new DataSet.DataView();
				dv.source(this.chart1.data).transform({
						type: 'map',
						callback: function callback(row) {
							row['支出'] *= -1;
							return row;
						}
					})
					.transform({
						type: 'fold',
						fields: ['收入', '支出'],
						key: 'opinion',
						value: 'value',
						retains: ['name']
					});
// 
				var colorMap = {
					'收入': '#86C7E6',
					'支出': '#1B85E7',
				};

				var chart = new G2.Chart({
					container: 'chart1',
					forceFit: _this.chart1.data.length <= 20 ? true : false,
					height: document.querySelector('#chart1').clientHeight,
					width: _this.chart1.data.length * 48,
					padding: [getRem(1.6), 0, 0, 0],
					// animate: false,
				});
				chart.source(dv);
				chart.axis(false);

				this.chart1.data.forEach(function(data, dataIndex) {
					if (data['支出'] > data['收入']) {
						// 辅助线
						// chart.guide().line({
						//   top: true,
						//   start: [dataIndex - .25, -data['收入']],
						//   end: [dataIndex + .25, -data['收入']],
						//
						//   lineStyle: {
						//     // fill: 'red',
						//     stroke: '#f2637b', // 线的颜色
						//     lineDash: null, // 虚线的设置
						//     lineWidth: 1 // 线的宽度
						//   }, // 图形样式配置
						//
						// });

						// 辅助框
						chart.guide().regionFilter({
							top: true, // 指定 giude 是否绘制在 canvas 最上层，默认为 false, 即绘制在最下层
							start: [dataIndex - .25, -data['收入']], // 辅助框起始位置，值为原始数据值，支持 callback
							end: [dataIndex + .25, -data['支出']], // 辅助框结束位置，值为原始数据值，支持 callback
							color: '#E2340D',
							// style: {
							//   lineWidth: 0, // 辅助框的边框宽度
							//   fill: '#e60308', // 辅助框填充的颜色
							//   fillOpacity: 1, // 辅助框的背景透明度
							//   stroke: '#e60303' // 辅助框的边框颜色设置
							// } // 辅助框的图形样式属性
						});
					}
				});


				chart.tooltip({
					inPlot: true,
					useHtml: true,
					htmlContent: function(title, items) {
						let data = {};
						_this.chart1.data.forEach(n => {
							if (n.name === title) {
                                data = n
                            }
                        })
                        return `<div class="g2-tooltip">
                    <div class="g2-tooltip-title">${title}</div>
                    <ul class="g2-tooltip-list">
                      <li class="g2-tooltip-li">设备数量：${_this.numberComma(data.设备数量)}</li>
                      <li class="g2-tooltip-li">设备价值：${_this.numberComma(Math.floor(Number(data.设备价值)))}万</li>
                      <li class="g2-tooltip-li">服务人次：${_this.numberComma(data.服务人次)}</li>
                      <li class="g2-tooltip-li">收入：${_this.numberComma(Math.floor(Number(data.收入)))}万</li>
                      <li class="g2-tooltip-li">支出：${_this.numberComma(Math.floor(Number(data.支出)))}万</li>
                    </ul>
                  </div>`
					}
				});
				//

				//                 chart.legend({
				//                     custom: true,
				// 					itemWidth: getRem(100),
				// 					autoWrap:true,
				//                     items: [
				//                         {
				// 							value: $.i18n.prop("dashboard.indexU.legendIn"), // 图例项的文本内容
				// 							marker: {
				// 								symbol: 'square',  // 该图例项 marker 的形状，参见 marker 参数的说明
				// 								fill: colorMap['收入'],  // 该图例项 marker 的填充颜色
				// 								radius: getRem(.1)
				// 							}
				// 						},
				// 						{
				// 							value: $.i18n.prop("dashboard.indexU.legendEx"), // 图例项的文本内容
				// 							marker: {
				// 								symbol: 'square',  // 该图例项 marker 的形状，参见 marker 参数的说明
				// 								fill: colorMap['支出'],  // 该图例项 marker 的填充颜色
				// 								radius: getRem(.1)
				// 							}
				// 						},
				// 						{
				// 						  value: $.i18n.prop("dashboard.radar.difference"), // 图例项的文本内容
				// 						  marker: {
				// 							symbol: 'square',  // 该图例项 marker 的形状，参见 marker 参数的说明
				// 							fill: 'red',  // 该图例项 marker 的填充颜色
				// 							radius: getRem(.1)
				// 						  }
				// 						},
				// 					],
				// 					position: 'top-left',
				// 					offsetX: getRem(0.2),
				// 					offsetY: getRem(-0.4),
				// 					// marker: {fill: 'red'},
				// 					textStyle: {
				// 						// textAlign: 'center', // 文本对齐方向，可取值为： start middle end
				// 						fill: '#000', // 文本的颜色
				// 						fontSize: getRem(.4), // 文本大小
				// 						// textBaseline: 'top' // 文本基准线，可取 top middle bottom，默认为middle
				// 					},
				// 
				// 					// onClick: ev => {
				// 					//   const item = ev.item;
				// 					//   const value = item.value;
				// 					//   const checked = ev.checked;
				// 					//   const geoms = chart.getAllGeoms();
				// 					//   for (let i = 0; i < geoms.length; i++) {
				// 					//               //     const geom = geoms[i];
				// 					//     Scale(), '///', value)
				// 					//     if (geom.getYScale().field === value) {
				// 					//       if (checked) {
				// 					//         geom.show();
				// 					//       } else {
				// 					//         geom.hide();
				// 					//       }
				// 					//     }
				// 					//   }
				// 					// }
				// 					hoverable: false,
				// 					reactive: false,
				// 					clickable: false,
				// 					// title: {
				// 					//   textAlign: 'center', // 文本对齐方向，可取值为： start middle end
				// 					//   // fill: '#404040', // 文本的颜色
				// 					//   fontSize: '50', // 文本大小
				// 					//   // fontWeight: 'bold', // 文本粗细
				// 					//   textBaseline: 'top' // 文本基准线，可取 top middle bottom，默认为middle
				// 					// }
				// 					// autoWrap: false,
				// 				});

				// chart.axis(false);
				chart.coord();
				chart.intervalStack().position('name*value').color('opinion', function(opinion) {
					return colorMap[opinion];
				});

				chart.on('click', ev => {
					// 获取逼近的点 point 的原始数据集合
					var records = chart.getSnapRecords({
						x: ev.x,
						y: ev.y
					});

					const name = records[0]._origin.name
					var item = _this.chart1.data.find(function(val) {
						return val.name == name
					})
					var id = item.id
					_this.$router.push({
						path: '/radar',
						query: {
							id
						}
					})
				});


				chart.render();
			},
			renderChart3() {
				let _this = this
				let total = 0;
				this.chart3.data.forEach(n => {
					if (n.count > 0)
						total += n.count
				})
				if(this.chart3.data[0].name=="暂无报修"){
					total = 0
				}
				let chart = new G2.Chart({
					container: 'chart3',
					forceFit: true,
					padding: 0,
					height: document.querySelector('#chart3').clientHeight,
					// animate: false,
				});
				chart.source(this.chart3.data, {
					// 'count': {
					//   formatter: function formatter(val) {
					//
					//     return val + 'aa';
					//   }
					// }
				});
				// 坐标轴
				chart.coord('theta', {
					radius: 0.75,
					innerRadius: 0.7
				});
				// 鼠标hover交互
				chart.tooltip({
					showTitle: false,
					'g2-tooltip': {
						'background-color': 'rgba(0, 0, 0, 0.7)',
						color: '#ddd',
					},
                    itemTpl: '<li style="font-size: .4rem;">{name}<br/><br/><span style="font-size:.36rem;">参报数量：'+((this.chart3.data[0].name=="暂无报修")?'0':'{value}')+'</span></li>'
                });
                // 辅助文本
                chart.guide().html({
                    position: ['50%', '50%'],
                    html: `<div class="total-repair" style="font-size: .4rem;text-align: center;width: 10em;cursor: pointer;">
                  <span style="font-size:.8rem;font-weight: bold;">${total}</span>件
                  <div style="margin-top: .25rem">今日总报修</div>
                </div>`,
                    alignX: 'middle',
                    alignY: 'middle'
				});
				// 不显示图例
				chart.legend(false)
				this.chart3.interval = chart.intervalStack()
					.position('count')
					.style({
						cursor: 'pointer'
					}).color('name*count', (name, count) => {
						// console.log(name,count)
						let c = count[1] - count[0]
						if (c === 0) {
							return '#eeeeee'
						}
					})
					.tooltip("name*count", (name, count) => {
						return {
							name: name,
							value: count,
							title: "参报数量："
						}
					})
				chart.render();

				this.addEvent(true)


				// 默认选中
				this.chart3.interval.setSelected(this.chart3.data[this.chart3.activeIndex]);
				this.getchart3List(this.chart3.activeIndex)

				chart.on('interval:click', ev => {
					const data = ev.data;
					if (data) {
						let name = data._origin['name'];
						// 重新开始定时，防止刚点击一项就自动跳到下一项
						this.chart3.data.forEach((n, i) => {
							if (n.name === name) {
								this.chart3.activeIndex = i
								this.getchart3List(this.chart3.activeIndex)
								clearInterval(this.chart3.timing)
								this.autoSelectchart3()
							}
						})
					}
				})
			},
			autoSelectchart3() {
				this.chart3.timing = setInterval(() => {
					this.chart3.activeIndex++
						(this.chart3.activeIndex > this.chart3.data.length - 1) && (this.chart3.activeIndex = 0)
					this.chart3.interval.setSelected(this.chart3.data[this.chart3.activeIndex])
					this.getchart3List(this.chart3.activeIndex)
				}, 30000)
			},
			getchart3List(index) {
				// 通过下标获取list
				var temp = []
				this.list3 = (this.chart3.data[index] == null) ? temp : this.chart3.data[index].list
				// 滚动条回到顶
				if (document.querySelector('.list-box3') != null)
					document.querySelector('.list-box3').scrollTo(0, 0)
			},

			numberComma(source, length = 3) {
				source = String(source).split(".");
				source[0] = source[0].replace(new RegExp('(\\d)(?=(\\d{' + length + '})+$)', 'ig'), "$1,");
				return source.join(".");
			},
			clickEvent() {
				window.open('', '_self') //跳转位置今日报修
			},
			addEvent(flag) {
				let ele = document.querySelector('.total-repair')
				if (!ele) return
				if (flag) {
					ele.addEventListener('click', this.clickEvent)
				} else {
					ele.document.querySelector('.total-repair').removeEventListener('click', this.clickEvent)
				}
			}
		},
		beforeDestroy() {
			this.addEvent(false)
		}
	}
</script>
<style scoped lang="less">
	.main-header {
		display: block;
	}
	
	/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar
{
    width: 0.32rem;
    height: 0.32rem;
    background-color: #F5F5F5;
}
 
/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 0.2rem;
    background-color: #F5F5F5;
}
 
/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb
{
    border-radius: 0.2rem;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
}

	.container {
		min-width: 1200px;
		width: 100%;
		height: 100%;
		background: #D7E0EE;
		overflow: hidden;
		font-size: .4rem;

		.content {
			padding: .3rem;
			width: 100%;
			height: 100%;
			/*height: calc(100% - 1.6rem);*/

			.top,
			.bottom {
				width: 100%;
				height: 55%;
				display: flex;

				.left {
					width: 55%;
				}

				.right {
					width: 45%;
				}

				.box {
					background: #fff;
					margin: .15rem;
					border-radius: .16rem;
				}

				.box1 {
					display: flex;
					flex-direction: column;
					background: transparent;

					.top-list-box {
						/*background: #D7E0EE;*/
						display: flex;
						padding-bottom: .3rem;

						.item {
							background: #fff;
							border-radius: .16rem;
							height: 2.6rem;
							flex: 1;
							margin-right: .3rem;
							padding: .5rem 0 .5rem 0;
							display: flex;
							flex-direction: column;
							justify-content: space-between;

							.value {
								color: #3D6EC0;
								font-size: .72rem;
								font-weight: bolder;
							}

							.text {
								font-size: .4rem;
							}

							&:last-of-type {
								margin-right: 0;
							}
						}
					}

					.chart1-box {
						background: #fff;
						width: 20.46rem;
						/*flex: 1;*/
						padding: .5rem;
						position: relative;
						border-radius: .16rem;
						height: calc(100% - 2.9rem);

						.list-box1 {
							position: absolute;
                            right: 0;
                            padding-right: .8rem;
                            left: 0;
                            top: .5rem;
                            display: flex;
                            justify-content: flex-end;
                            z-index: 2;

                            .list {
                                display: flex;
                                align-items: baseline;
                                margin-left: .85rem;

                                .total {
                                    margin-right: .6rem;

									.value {
										font-weight: bold;
										font-size: .6rem;
										margin-bottom: .1rem;
									}

									.text {
										font-size: .4rem;
									}
								}

								.status {


									.value {
										font-weight: bold;
										font-size: .6rem;
										display: flex;
										align-items: baseline;
										margin-bottom: .1rem;

										.iconfont {
											font-size: .7rem;
										}
									}

									.text {
										color: #000;
										font-size: .4rem;
									}

									&.up {}

									&.red {
										color: #E64340;
									}

									&.green {
										color: #3eaf7c;
									}

									&.down {

										.iconfont {
											transform: rotate(180deg);
										}
									}
								}
							}
						}

						#chart1 {
							width: 100%;
							height: 100%;
							overflow-x: auto;
							overflow-y: hidden;
						}
					}
				}

				.box2 {
					position: relative;


				}

				.box3 {
					display: flex;

					#chart3 {
						width: 40%;
						height: 100%;
					}

					.list-box-container {
						width: 60%;
						padding: .6rem 0 .6rem .4rem;
						/*flex: 1;*/

						.list-box3 {
							padding-right: .9rem;
							height: 100%;
							overflow-y: auto;

							.repairNull {
								font-size: 0.6rem;
								text-align: center;
							}

							.list {
								display: flex;
								justify-content: space-between;
								align-items: center;
								margin-bottom: .4rem;

								&:last-of-type {
									margin-bottom: 0;
								}

								.index {
									font-size: .54rem;
									color: #5a81c4;
									margin-right: .35rem;
								}

								.name {
									flex: 1;
									margin-right: .4rem;
									width: 0;
								}

								.status {
									font-size: .36rem;
									padding: .08rem .14rem;
									border-radius: .08rem;
									background: #5975AE;
									color: #fff;

									&.err {
										background: #E2340D;
									}
								}
							}
						}
					}

				}

				.box4 {
					padding: 0 .8rem;
				}
			}

			.bottom {
				height: 45%;
			}


			/deep/ .g2-tooltip {
				position: absolute;
				background-color: rgba(0, 0, 0, 0.7);
				border-radius: .16rem;
				color: #ddd;
				white-space: nowrap;
				font-size: .36rem;
				line-height: 1.2;
				padding: .24rem .24rem .1rem .24rem;
				box-shadow: 0 0 .1rem #aeaeae;
			}

			/deep/ .g2-tooltip-title {
				font-size: .4rem;
			}

			/deep/ .g2-tooltip-list {
				padding: 0;
				list-style-type: none;
				margin-top: .2rem;

				.g2-tooltip-li {
					margin: .2rem 0;
					font-size: 0.3rem;
				}
			}

			canvas {
				background: #fff;
				width: 100%;
				height: 100%;
			}
		}
	}
</style>



